<template>
  <section class="section section-color-gray">
    <div class="section-header">
      <h2 class="section-heading-eyebrow">Processus</h2>
      <h3 class="section-heading">
        Mon plan de travail<br />pour créer votre site web.
      </h3>
    </div>
    <div class="sticky-card-gap"></div>
    <div class="container container-large">
        <div class="stack-cards js-stack-cards">
          <div class="stack-cards__item js-stack-cards__item">
            <h2><span>Étape 1</span>Demande de Projet</h2>
            <p>
              Au début de notre collaboration se trouve la demande de projet.
              Vos informations m'aident à comprendre si je suis la bonne personne pour votre projet.
              Si oui, nous organiserons un rendez-vous pour que je puisse en savoir plus.
            </p>
          </div>
          <div class="stack-cards__item js-stack-cards__item">
            <h2><span>Étape 2</span>Première Rencontre</h2>
            <p>
              Une confiance mutuelle doit être installée pour que nous puissions travailler ensemble.
              Lors de notre première rencontre, nous apprendrons à nous connaître et à voir si nous sommes bien adaptés.
              Nous discutons également des objectifs et des exigences exacts, clarifions les questions et définissons les prochaines étapes.
            </p>
          </div>
          <div class="stack-cards__item js-stack-cards__item">
            <h2><span>Étape 3</span>Proposition</h2>
            <p>
              Après en avoir appris le plus possible sur votre projet, je crée une proposition individuelle.
              Je ne facture pas mon travail à l'heure, mais par projet.
              De cette façon, vous avez un aperçu complet des coûts.
            </p>
          </div>
          <div class="stack-cards__item js-stack-cards__item">
            <h2><span>Étape 4</span>Mise en Oeuvre</h2>
            <p>
              Après acceptation de ma proposition, une maquette grossière du fonctionnemlent sera réalisée et validée par vos soins.
              Dès lors que j'ai en ma possession tous les outils nécessaires, je commence le développement de votre site.
              Il est important de pouvoir rester en contact et ainsi preserver une certaine transparence sur votre projet.
            </p>
          </div>
          <div class="stack-cards__item js-stack-cards__item">
            <h2><span>Étape 5</span>Mise en Ligne</h2>
            <p>
              Avant le lancement, le site Web finit passe par une assurance qualité interne au cours de laquelle il sera mis à l'épreuve.
              Respect des normes de codage, adaptation du site sur différents périphériques etc...
              L'hébergement du site est à votre charge mais je pourrais vous aider à bien le choisir.
            </p>
          </div>
        </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Process2',
  data () {
    return {
    }
  },
  methods: {},
  mounted () {
    var StackCards = function (element) {
      this.element = element
      this.items = this.element.getElementsByClassName('js-stack-cards__item')
      this.scrollingFn = false
      this.scrolling = false
      initStackCardsEffect(this)
      initStackCardsResize(this)
    }

    function initStackCardsEffect (element) { // use Intersection Observer to trigger animation
      setStackCards(element) // store cards CSS properties
      var observer = new IntersectionObserver(stackCardsCallback.bind(element), { threshold: [0, 1] })
      observer.observe(element.element)
    }

    function initStackCardsResize (element) { // detect resize to reset gallery
      element.element.addEventListener('resize-stack-cards', function () {
        setStackCards(element)
        animateStackCards.bind(element)
      })
    }

    function stackCardsCallback (entries) { // Intersection Observer callback
      if (entries[0].isIntersecting) {
        if (this.scrollingFn) return // listener for scroll event already added
        stackCardsInitEvent(this)
      } else {
        if (!this.scrollingFn) return // listener for scroll event already removed
        window.removeEventListener('scroll', this.scrollingFn)
        this.scrollingFn = false
      }
    };

    function stackCardsInitEvent (element) {
      element.scrollingFn = stackCardsScrolling.bind(element)
      window.addEventListener('scroll', element.scrollingFn)
    };

    function stackCardsScrolling () {
      if (this.scrolling) return
      this.scrolling = true
      window.requestAnimationFrame(animateStackCards.bind(this))
    };

    function setStackCards (element) {
      // store wrapper properties
      element.marginY = '10px'
      getIntegerFromProperty(element) // convert element.marginY to integer (px value)
      element.elementHeight = element.element.offsetHeight

      // store card properties
      var cardStyle = getComputedStyle(element.items[0])
      element.cardTop = Math.floor(parseFloat(cardStyle.getPropertyValue('top')))
      element.cardHeight = Math.floor(parseFloat(cardStyle.getPropertyValue('height')))

      // store window property
      element.windowHeight = window.innerHeight

      // reset margin + translate values
      if (isNaN(element.marginY)) {
        element.element.style.paddingBottom = '0px'
      } else {
        element.element.style.paddingBottom = (element.marginY * (element.items.length - 1)) + 'px'
      }

      for (var i = 0; i < element.items.length; i++) {
        if (isNaN(element.marginY)) {
          element.items[i].style.transform = 'none;'
        } else {
          element.items[i].style.transform = 'translateY(' + element.marginY * i + 'px)'
        }
      }
    }

    function getIntegerFromProperty (element) {
      var node = document.createElement('div')
      node.setAttribute('style', 'opacity:0; visbility: hidden;position: absolute; height:' + element.marginY)
      element.element.appendChild(node)
      element.marginY = parseInt(getComputedStyle(node).getPropertyValue('height'))
      element.element.removeChild(node)
    };

    function animateStackCards () {
      if (isNaN(this.marginY)) { // --stack-cards-gap not defined - do not trigger the effect
        this.scrolling = false
        return
      }

      var top = this.element.getBoundingClientRect().top

      if (this.cardTop - top + this.element.windowHeight - this.elementHeight - this.cardHeight + this.marginY + this.marginY * this.items.length > 0) {
        this.scrolling = false
        return
      }

      for (var i = 0; i < this.items.length; i++) { // use only scale
        var scrolling = this.cardTop - top - i * (this.cardHeight + this.marginY)
        if (scrolling > 0) {
          var scaling = i === this.items.length - 1 ? 1 : (this.cardHeight - scrolling * 0.05) / this.cardHeight
          this.items[i].style.transform = 'translateY(' + this.marginY * i + 'px) scale(' + scaling + ')'
        } else {
          this.items[i].style.transform = 'translateY(' + this.marginY * i + 'px)'
        }
      }

      this.scrolling = false
    };

    // initialize StackCards object
    var stackCards = document.getElementsByClassName('js-stack-cards')

    if (stackCards.length > 0) {
      var stackCardsArray = []
      for (var i = 0; i < stackCards.length; i++) {
        (function (i) {
          stackCardsArray.push(new StackCards(stackCards[i]))
        })(i)
      }

      var resizingId = false
      var customEvent = new CustomEvent('resize-stack-cards')

      window.addEventListener('resize', function () {
        clearTimeout(resizingId)
        resizingId = setTimeout(doneResizing, 500)
      })
    }
    function doneResizing () {
      for (var i = 0; i < stackCardsArray.length; i++) {
        (function (i) { stackCardsArray[i].element.dispatchEvent(customEvent) })(i)
      }
    }
  }
}
</script>

<style scoped>
.stack-cards {
  margin-top: 100px;
  padding-top: 100px;
  padding-bottom: 1000px;
  max-width: 700px;
  margin: 0 auto;
}
.stack-cards h2,
.stack-cards span {
  padding-bottom: 10px;
}
.stack-cards span {
  display: block;
  font-size: 18px;
  color: #0fa7ff;
}
.stack-cards__item {
  position: sticky;
  top: 20em;
  height: 400px;
  border: 1px solid #ccc;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
  background-color: #fefefe;
  color: #333;
  padding: 80px;
  border-radius: 28px;
  margin-bottom: 170px;
  transform-origin: center top;
}
.sticky-card-gap {
  height: 30vh;
}
@media screen and (max-width: 479px) {
  .stack-cards__item {
    top: 10em;
    height: 350px;
    border-radius: 22px;
    padding: 40px
  }
  .stack-cards h2 {
    font-size: 24px;
  }
  .stack-cards span {
    display: block;
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.1em;
    color: #0fa7ff;
  }
  .stack-cards p {
    font-size: 14px;
  }
  .stack-cards h2,
  .stack-cards span {
    padding-bottom: 0px;
  }
}
</style>



