<template>
  <div class="modal" ref="modal">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
         <div class="modal-header">
          <img :src="product.full" />
          <button type="button" class="close" @click="hideDialog">
            <span>&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="product-info">
            <div class="product-category">
              <div class="product-category__techno">{{ product.techno }}</div>
            </div>
            <h4 class="product-title">{{ product.title }}</h4>
          </div>
          <img :src="product.presentational" />
          <div class="product-info-wrap__link" v-if="product.activatedLink === true">
            <a :href="product.link" class="product-info__link" target="_blank">Voir le site</a>
          </div>
        </div>
        <div class="modal-footer">
          <div class="carousel slide" data-interval="2000" data-ride="carousel">
            <div class="carousel-inner">
              <div class="carousel-item" v-for="(image, index) in product.carousel" :key="image.id" :class="{ 'active': index === 0 }">
                <img class="d-block w-100" :src="image" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'ProductModal2',
  props: {
    offsetY: {
      type: Number,
      required: true
    },
    offsetX: {
      type: Number,
      required: true
    },
    product: {
      type: Object,
      required: true,
      carousel: {
        type: Object,
        required: true
      }
    }
  },
  methods: {
    hideDialog () {
      this.$emit('hideDialog')
    }
  },
  watch: {
    offsetX (newVal) {
      console.log('new val: ', newVal)
    },
    offsetY (newVal) {
      console.log('new val: ', newVal)
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  visibility: hidden;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.75);
}
.modal .close {
  position: absolute;
  right: 30px !important;
  top: 30px !important;
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(193, 193, 193, .9);
  border-radius: 50%;
  font-size: 1.8rem;
  padding: 0;
  display: block;
  cursor: none;
}
.modal-img {
  position: relative;
  top: 0;
  left: 0;
  border-radius: 28px 28px 0 0;
  width: 100%;
  height: 100%;
}
.modal-dialog {
  width: 100%;
  height: 100%;
  margin: 0;
}
.modal-content {
  background-color: #FEFEFE;
  border-radius: 28px 28px 0 0;
}
.modal-header {
  padding: 0;
  border-radius: 28px 28px 0 0;
  overflow: hidden;
}
.modal-body {
  display: block;
  padding: 0;
  text-align: center;
}
.product-info {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  max-width: 560px;
  margin-right: auto;
  margin-bottom: 56px;
  margin-left: auto;
  padding-top: 64px;
  padding-right: 18px;
  padding-left: 18px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  text-align: center;
}
.product-category {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 12px;
  padding: 6px 16px;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 10px;
  background-color: hsla(0, 0%, 46%, .12);
}
.product-category__techno {
  opacity: .65;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -.025em;
}
.product-title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 64px;
  line-height: 1.2;
  letter-spacing: -.045em;
}
.product-info-wrap {
  position: relative;
  z-index: 2;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 56px;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.product-info-wrap__link {
  padding-bottom: 20px;
}
.product-info__link {
  display: inline-block;
  padding: 10px 28px;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 12px;
  background-color: #006ddb;
  -webkit-transition: background-color .2s;
  transition: background-color .2s;
  color: #FEFEFE;
  font-weight: 500;
  text-decoration: none;
}
.modal-footer {
  padding: 0;
  border: none;
}
.modal-footer > * {
  margin: 0;
}
</style>



