<template>
    <figure id="preview" class="preview-image"
    ref="card"
    @mousemove="cardMove"
    @mouseleave="cardLeave"
    >
      <button type="button" class="close">
        <span>&times;</span>
      </button>
      <img
        :src="prod.thumb"
        @click="showDialog($event, prod)"
      >
    </figure>
</template>
<script>
export default {
  name: 'ProductList2',
  props: {
    prod: {
      type: Object,
      required: true
    },
    modalOpen: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data () {
    return {
      openProductId: null,
      debounce: null // 3d card
    }
  },
  methods: {
    showDialog (e, product) {
      this.openProductId = product.id
      const pos = e.target.parentNode.getBoundingClientRect()
      this.$emit('showDialog', {
        product: product,
        bounds: pos
      })
    },
    cardMove (e) {
      const card = this.$refs.card
      const relX = (e.offsetX + 1) / card.offsetWidth
      const relY = (e.offsetY + 1) / card.offsetHeight
      const rotY = `rotateY(${(relX - 0.5) * 5}deg)` // 5 = angle
      const rotX = `rotateX(${(relY - 0.5) * -5}deg)`
      card.style.transform = `perspective(1000px) ${rotY} ${rotX}`
      card.style.transition = `transform 100ms ease`
    },
    cardLeave () {
      const card = this.$refs.card
      card.style.transform = `perspective(1000px)`
      card.style.transition = `transform 350ms ease-in`
    }
  },
  watch: {
    viewerOpen (newVal) {
      if (!newVal) {
        // 1450 is the duration of the leave transition
        // doing it shortly before to prevent any jerkiness
        setTimeout(() => {
          this.openProductId = null
        }, 1400)
      }
    }
  }
}
</script>

<style scoped>
.preview-image {
  position: relative;
  margin: 0;
}
.preview-image img {
  border-radius: 28px;
  transform: scale(1, 1);
  transition: all 350ms;
}
.preview-image:hover img {
  transform: scale(1.03, 1.03);
  box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}
.close {
  display: none;
}
.preview-image:hover .close {
  transform: rotate(45deg);
  position: absolute;
  z-index: 1;
  right: 0px !important;
  top: 6px !important;
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(193, 193, 193, .9);
  border-radius: 50%;
  font-size: 1.8rem;
  padding: 0;
  display: block;
  cursor: none;
  pointer-events: none;
}
.preview-image.is-hidden::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #404040;
}
</style>



