<template>
  <section id="projects" class="section section-color-gray">
    <div class="section-header">
      <h2 class="section-heading-eyebrow">Projets</h2>
      <h3 class="section-heading">Selection de projets réalisés.</h3>
    </div>
    <div class="container container-large">
      <div class="layout-grid intro-grid">
        <ProductList2
          v-for="(product, index) in productsLoaded"
          :key="index"
          :prod="product"
          :modal-open="modal.open"
          @showDialog="showDialog"
        />
      </div>
    </div>
    <div class="row">
      <div id="loader" class="col-12 text-center">
      <a class="load-more" @click="loadMoreProducts">Voir plus</a>
      </div>
    </div>
    <transition
      @before-enter="beforeEnter"
      @enter="enter"
      @after-enter="afterEnter"
      @before-leave="beforeLeave"
      @leave="leave"
      @after-leave="afterLeave"
    >
      <ProductModal2
        :product="modal.product"
        v-show="modal.open"
        :offsetX="this.modal.offsetX"
        :offsetY="this.modal.offsetY"
        @hideDialog="hideDialog"
      />
    </transition>
  </section>
</template>

<script>
import anime from 'animejs'
import ProductList2 from './ProductList2'
import ProductModal2 from './ProductModal2'
export default {
  name: 'Products2',
  components: {
    ProductList2,
    ProductModal2
  },
  data () {
    return {
      modal: {
        animation: null,
        open: false,
        openTran: null,
        expandTran: null,
        offsetX: 0,
        offsetY: 0,
        initialWidth: 0,
        product: {
          id: ''
        }
      },
      products: [
        {
          id: '1',
          title: 'Business Agency',
          techno: 'Bootstrap',
          link: 'http://citrus.design.free.fr/themes/lea/index.html',
          thumb: require('../assets/img/projects/lea/lea_template.jpg'),
          full: require('../assets/img/projects/lea/lea_template.jpg'),
          presentational: require('../assets/img/projects/lea/lea_present_img_01.jpg'),
          carousel: [
            require('../assets/img/projects/lea/lea_img_01.jpg'),
            require('../assets/img/projects/lea/lea_img_02.jpg'),
            require('../assets/img/projects/lea/lea_img_03.jpg'),
            require('../assets/img/projects/lea/lea_img_04.jpg'),
            require('../assets/img/projects/lea/lea_img_05.jpg'),
            require('../assets/img/projects/lea/lea_img_06.jpg'),
            require('../assets/img/projects/lea/lea_img_07.jpg')
          ],
          activatedLink: true
        },
        {
          id: '2',
          title: 'CV Personnel',
          techno: 'Bootstrap',
          link: '/projects/teo/index.html',
          thumb: require('../assets/img/projects/teo/teo_template.jpg'),
          full: require('../assets/img/projects/teo/teo_template.jpg'),
          presentational: require('../assets/img/projects/lea/lea_present_img_01.jpg'),
          carousel: [
            require('../assets/img/projects/teo/teo_img_01.jpg'),
            require('../assets/img/projects/teo/teo_img_02.jpg'),
            require('../assets/img/projects/teo/teo_img_03.jpg'),
            require('../assets/img/projects/teo/teo_img_04.jpg'),
            require('../assets/img/projects/teo/teo_img_05.jpg'),
            require('../assets/img/projects/teo/teo_img_06.jpg'),
            require('../assets/img/projects/teo/teo_img_07.jpg'),
            require('../assets/img/projects/teo/teo_img_08.jpg')
          ],
          activatedLink: true
        },
        {
          id: '3',
          title: 'West Peinture',
          techno: 'Logo Illustrator',
          link: '',
          thumb: require('../assets/img/projects/wp_logo/logo.jpg'),
          full: require('../assets/img/projects/wp_logo/logo.jpg'),
          presentational: require('../assets/img/projects/wp_logo/wp_present_logo.jpg'),
          carousel: [
          ],
          activatedLink: false
        },
        {
          id: '4',
          title: 'Velo\'V',
          techno: 'Application Javascript',
          link: '/projects/velov/index.html',
          thumb: require('../assets/img/projects/velov/velov.jpg'),
          full: require('../assets/img/projects/velov/velov.jpg'),
          presentational: require('../assets/img/projects/velov/velov_present_img_01.jpg'),
          carousel: [
          ],
          activatedLink: true
        },
        {
          id: '5',
          title: 'Decan',
          techno: 'Logo Illustrator',
          link: '',
          thumb: require('../assets/img/projects/decan/logo.jpg'),
          full: require('../assets/img/projects/decan/logo.jpg'),
          presentational: require('../assets/img/projects/decan/decan_present_logo.jpg'),
          carousel: [
          ],
          activatedLink: false
        }
      ],
      productsToShow: 4,
      totalCount: 4
    }
  },
  computed: {
    productsLoaded () {
      return this.products.slice(0, this.productsToShow)
    }
  },
  methods: {
    showDialog ({product, bounds}) {
      this.modal.product = product
      this.modal.offsetY = bounds.top
      this.modal.offsetX = bounds.left
      this.modal.initialWidth = bounds.width
      this.modal.initialHeight = bounds.height
      this.modal.open = true
    },
    hideDialog () {
      this.modal.open = false
    },
    beforeEnter (el) {
      el.style.top = `${this.modal.offsetY}px`
      el.style.left = `${this.modal.offsetX}px`
      el.style.width = `${this.modal.initialWidth}px`
      el.style.height = `${this.modal.initialHeight}px`
      el.style.visibility = `visible`
      document.body.style.overflow = `hidden`
    },
    enter (el, done) {
      // slide the product view to the center
      const animation = anime.timeline()
      animation.add({
        targets: el,
        top: [this.modal.offsetY, 0],
        left: [this.modal.offsetX, 0],
        width: `100%`,
        height: `100vh`,
        easing: 'easeInOutQuad',
        duration: 500
      }).add({
        targets: el,
        paddingTop: `65px`,
        duration: 500
      })

      animation.complete = done
    },
    afterEnter (el) {
      const animation = anime.timeline()
      animation.complete = function () {
        el.style.cssText += 'margin: 0; top: 0; left: 0;'
      }
    },
    beforeLeave (el) {
      /* remove margin, width, height and set top and left back to offsets */
      const windowWidth = window.innerWidth
      const windowHeight = window.innerHeight
      const windowCenterX = (windowWidth - (this.modal.initialWidth * 2)) / 2
      const windowCenterY = (windowHeight - (this.modal.initialHeight * 2)) / 2
      el.style.top = `${windowCenterY}px`
      el.style.left = `${windowCenterX}px`
      el.style.width = ``
      el.style.height = ``
      el.style.margin = ''
    },
    leave (el, done) {
      // slide the product view to original state
      const animation = anime.timeline()
      animation.add({
        targets: '.modal',
        paddingTop: `-65px`,
        easing: 'easeInOutQuad',
        duration: 250
      }).add({
        targets: '.modal',
        top: [0, this.modal.offsetY],
        left: [0, this.modal.offsetX],
        width: `${this.modal.initialWidth}px`,
        height: `${this.modal.initialHeight}px`,
        easing: 'easeInOutQuad',
        duration: 500
      })

      animation.complete = done
      document.body.style.overflow = ``
    },
    afterLeave (el) {
      el.style.visibility = `hidden`
    },
    loadMoreProducts () {
      if (this.productsToShow > this.products.length) return
      this.productsToShow = this.productsToShow + 2

      if (this.totalCount === this.products.length) {
        const loader = document.getElementById('loader')
        loader.parentNode.removeChild(loader)
      }
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
a.load-more {
  position: relative;
  z-index: 1001;
  display: inline-block;
  margin-top: 24px;
  padding: 8px 22px;
  font-size: 22px;
  font-weight: 700;
  opacity: 0.75;
  letter-spacing: -0.01em;
  text-decoration: none;
  border: none;
  color: #0fa7ff
}
</style>



