<template>
  <div class="magic-cursor">
    <div id="MagicCursor" :class="[ 'mCursor', { 'mCursor-hide': mCursorHide, 'isSticked': isSticked } ]" :style="mCursorStyle">
      <div class="mCursor-wrap">
        <div class="mCursor-content" :style="mCursorContent"></div>
      </div>
    </div>
    <div :class="{ 'mCursor-highlight': activeHighLight }"  :style="mCursorHighlight"></div>
  </div>
</template>

<script>
export default {
  name: 'MagicCursor3',
  props: {
    targets: Array,
    dotColor: String,
    magnetDist: Number
  },
  data () {
    return {
      mCursorX: 0,
      mCursorY: 0,
      mCursorRadius: '50%',
      mCursorTransX: 0,
      mCursorTransY: 0,
      targetTransX: 0,
      targetTransY: 0,
      activeHighLight: true,
      centerX: 0,
      centerY: 0,
      mCursorHighOpa: 0,
      targetBoundsW: 0,
      targetBoundsH: 0,
      isSticked: false,
      mCursorHide: false
    }
  },
  computed: {
    mCursorStyle () {
      return {
        top: `${this.mCursorY}px`,
        left: `${this.mCursorX}px`,
        width: `${this.targetBoundsW}px`,
        height: `${this.targetBoundsH}px`,
        zIndex: `2000`,
        transform: `translate(${this.mCursorTransX}px, ${this.mCursorTransY}px)`,
        transitionDuration: `150ms`
      }
    },
    mCursorContent () {
      return {
        opacity: `0.5`,
        borderRadius: `${this.mCursorRadius}`,
        backgroundColor: `${this.dotColor}`
      }
    },
    mCursorHighlight () {
      return {
        top: `${this.centerY}px`,
        left: `${this.centerX}px`,
        width: `${this.targetBoundsW / 2}px`,
        height: `${this.targetBoundsH / 2}px`,
        opacity: `${this.mCursorHighOpa}`,
        filter: `blur(${this.targetBoundsH / 2}px)`
      }
    }
  },
  methods: {
    mCursorMove (e) {
      // cursor position
      this.mCursorX = e.clientX
      this.mCursorY = e.clientY

      if ((this.targets.length > 0 &&
         this.targets.includes(e.target.tagName.toLowerCase())) ||
         this.targets.filter(c => e.target.className.split(' ').includes(c)).length > 0) {
        // Activate stick cursor
        this.isSticked = true

        // Get bounds of the target on mouse over
        const targetBoundsL = e.target.getBoundingClientRect().left
        const targetBoundsT = e.target.getBoundingClientRect().top

        // Replace initial cursor dim by target dim
        this.targetBoundsW = e.target.getBoundingClientRect().width
        this.targetBoundsH = e.target.getBoundingClientRect().height

        // Target translation if cursor is sticked
        this.targetTransX = (this.mCursorX - (targetBoundsL + this.targetBoundsW / 2)) * (this.magnetDist / 100)
        this.targetTransY = (this.mCursorY - (targetBoundsT + this.targetBoundsH / 2)) * (this.magnetDist / 100)

        // Center cursor on target elm
        this.centerX = targetBoundsL + (this.targetBoundsW / 2)
        this.centerY = targetBoundsT + (this.targetBoundsH / 2)

        if (this.isSticked) {
          this.mCursorTransX = this.centerX - this.mCursorX
          this.mCursorTransY = this.centerY - this.mCursorY
          e.target.style.transform = `translate(${this.targetTransX}px, ${this.targetTransY}px)`
        }
      }
    },
    mCursorOver (e) {
      // Filter the prop targets
      if ((this.targets.length > 0 &&
         this.targets.includes(e.target.tagName.toLowerCase())) ||
         this.targets.filter(c => e.target.className.split(' ').includes(c)).length > 0) {
        // Apply radius & highlight on cursor content
        this.mCursorRadius = `10px`
        this.mCursorHighOpa = `1`
      }
    },
    mCursorOut (e) {
      // Default Dim of cursor
      this.targetBoundsW = '20'
      this.targetBoundsH = '20'
      // Default radius & highlight on cursor content
      this.mCursorRadius = `50%`
      this.mCursorHighOpa = `0`
      // Set default translate
      this.mCursorTransX = 0
      this.mCursorTransY = 0
      e.target.style.transform = ``
      // Remove stick cursor
      this.isSticked = false
    }
  },
  mounted () {
    document.addEventListener('mouseenter', e => {
      this.mCursorHide = false
      this.activeMagnet = false
    })
    document.addEventListener('mousemove', this.mCursorMove)
    document.addEventListener('mouseover', this.mCursorOver)
    document.addEventListener('mouseout', this.mCursorOut)
    document.addEventListener('mouseleave', e => { this.mCursorHide = true })
  }
}
</script>

<style scoped>
body:hover .mCursor {
  opacity: 1;
}
.mCursor {
  position: fixed;
  z-index: 999;
  width: 20px;
  height: 20px;
  opacity: 0;
  pointer-events: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: opacity 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: opacity 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.mCursor-wrap {
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: opacity 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), transform 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), -webkit-transform 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.mCursor-content {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0.5;
  pointer-events: none;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: opacity 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition: opacity 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), height 0.2s cubic-bezier(0.215, 0.61, 0.355, 1), width 0.2s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.mCursor-highlight {
  position: fixed;
  z-index: 1001;
  border-radius: 50%;
  background-color: #fefefe;
  opacity: 0;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.isSticked {
  transition-property: width, height, opacity;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-duration: 200ms;
  -o-transition-property: width, height, opacity;
  -o-transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  -o-transition-duration: 200ms;
  -moz-transition-property: width, height, opacity;
  -moz-transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  -moz-transition-duration: 200ms;
  -webkit-transition-property: width, height, opacity;
  -webkit-transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  -webkit-transition-duration: 200ms;
  opacity: 0.6 !important;
}
.mCursor-hide {
  opacity: 0;
}

@media (max-width: 479px) {
  .magic-cursor {
    display: none;
  }
}
</style>



