<template>
    <footer class="footer section-color-gray">
        <div class="container">
            <div class="divider"></div>
            <div class="footer-menu-wrap">
                <div class="footer-copyright">
                    <p>Copyright © <span id="copyright-year"></span> <a href="#">Alexandre Jacquin</a> | Tous droits réservés.</p>
                </div>
            </div>
        </div>
    </footer>
</template>

<script>
export default {
  name: 'Footer',
  mounted () {
    document.querySelector('#copyright-year').innerText = new Date().getFullYear()
  }
}
</script>

<style scoped>
.divider {
  width: 100%;
  height: 1px;
  background-color: hsla(0,0%,53.3%,.2);
}
.footer-menu-wrap {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
  padding-bottom: 18px;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  font-size: 13px;
  font-weight: 500;
  letter-spacing: -.011em;
}
.footer-copyright p {
  opacity: .75;
  color: #FEFEFE
}
</style>



